import * as tf from '@tensorflow/tfjs';
import SJS  from 'discrete-sampling';

async function loadingModel(path) {
    return new Promise ((resolve, reject) => {
        tf.loadLayersModel(path)
        .then(function (res) {
            resolve(res);
        })
    .catch(function (error) {
            reject(error);
        });
    });
};

class SentimentPredictor {
    /**
     * Initializes the Sentiment demo.
     */
    async init(modelName) {
        this.model = await loadingModel('./public/models/' + modelName + '/model.json');
        this.maxLen = 10;
        if (modelName.substring(0,9) == "nombres-b" || modelName.substring(0,9) == "nombres-2" || modelName.substring(0,9) == "nombres-o") {
            this.charIndices = {'\n': 0, "'": 1, 'A': 2, 'B': 3, 'C': 4, 'D': 5, 'E': 6, 'F': 7, 'G': 8, 'H': 9, 'I': 10, 'J': 11, 'K': 12, 'L': 13, 'M': 14, 'N': 15, 'O': 16, 'P': 17, 'Q': 18, 'R': 19, 'S': 20, 'T': 21, 'U': 22, 'V': 23, 'W': 24, 'X': 25, 'Y': 26, 'Z': 27, 'a': 28, 'b': 29, 'c': 30, 'd': 31, 'e': 32, 'f': 33, 'g': 34, 'h': 35, 'i': 36, 'j': 37, 'k': 38, 'l': 39, 'm': 40, 'n': 41, 'o': 42, 'p': 43, 'q': 44, 'r': 45, 's': 46, 't': 47, 'u': 48, 'v': 49, 'w': 50, 'x': 51, 'y': 52, 'z': 53, 'ç': 54, 'ñ': 55};
        }
        else if (modelName.substring(0,7) == "english") {
            this.charIndices = {'\n': 0, 'A': 1, 'B': 2, 'C': 3, 'D': 4, 'E': 5, 'F': 6, 'G': 7, 'H': 8, 'I': 9, 'J': 10, 'K': 11, 'L': 12, 'M': 13, 'N': 14, 'O': 15, 'P': 16, 'Q': 17, 'R': 18, 'S': 19, 'T': 20, 'U': 21, 'V': 22, 'W': 23, 'X': 24, 'Y': 25, 'Z': 26, 'a': 27, 'b': 28, 'c': 29, 'd': 30, 'e': 31, 'f': 32, 'g': 33, 'h': 34, 'i': 35, 'j': 36, 'k': 37, 'l': 38, 'm': 39, 'n': 40, 'o': 41, 'p': 42, 'q': 43, 'r': 44, 's': 45, 't': 46, 'u': 47, 'v': 48, 'w': 49, 'x': 50, 'y': 51, 'z': 52};
        }
        else if (modelName.substring(0,9) == "nombres-c") {
            this.charIndices = {'\n': 0, 'A': 1, 'B': 2, 'C': 3, 'D': 4, 'E': 5, 'F': 6, 'G': 7, 'H': 8, 'I': 9, 'J': 10, 'K': 11, 'L': 12, 'M': 13, 'N': 14, 'R': 15, 'S': 16, 'T': 17, 'U': 18, 'V': 19, 'W': 20, 'X': 21, 'Y': 22, 'Z': 23, 'a': 24, 'b': 25, 'c': 26, 'd': 27, 'e': 28, 'f': 29, 'g': 30, 'h': 31, 'i': 32, 'j': 33, 'k': 34, 'l': 35, 'm': 36, 'n': 37, 'o': 38, 'p': 39, 'q': 40, 'r': 41, 's': 42, 't': 43, 'u': 44, 'v': 45, 'w': 46, 'x': 47, 'y': 48, 'z': 49, 'é': 50};
        }
        else if (modelName.substring(0,14) == "nombres-pill-c") {
            this.charIndices = {'\n': 0, "'": 1, 'A': 2, 'B': 3, 'C': 4, 'D': 5, 'E': 6, 'F': 7, 'G': 8, 'H': 9, 'I': 10, 'J': 11, 'K': 12, 'L': 13, 'M': 14, 'N': 15, 'O': 16, 'P': 17, 'Q': 18, 'R': 19, 'S': 20, 'T': 21, 'U': 22, 'V': 23, 'W': 24, 'X': 25, 'Y': 26, 'Z': 27, 'a': 28, 'b': 29, 'c': 30, 'd': 31, 'e': 32, 'f': 33, 'g': 34, 'h': 35, 'i': 36, 'j': 37, 'k': 38, 'l': 39, 'm': 40, 'n': 41, 'o': 42, 'p': 43, 'q': 44, 'r': 45, 's': 46, 't': 47, 'u': 48, 'v': 49, 'w': 50, 'x': 51, 'y': 52, 'z': 53, 'é': 54, 'ñ': 55};
        }
        else {
            this.charIndices = {'\n': 0, "'": 1, 'A': 2, 'B': 3, 'C': 4, 'D': 5, 'E': 6, 'F': 7, 'G': 8, 'H': 9, 'I': 10, 'J': 11, 'K': 12, 'L': 13, 'M': 14, 'N': 15, 'O': 16, 'P': 17, 'Q': 18, 'R': 19, 'S': 20, 'T': 21, 'U': 22, 'V': 23, 'W': 24, 'X': 25, 'Y': 26, 'Z': 27, 'a': 28, 'b': 29, 'c': 30, 'd': 31, 'e': 32, 'f': 33, 'g': 34, 'h': 35, 'i': 36, 'j': 37, 'k': 38, 'l': 39, 'm': 40, 'n': 41, 'o': 42, 'p': 43, 'q': 44, 'r': 45, 's': 46, 't': 47, 'u': 48, 'v': 49, 'w': 50, 'x': 51, 'y': 52, 'z': 53, 'ñ': 54};
        }
        
        this.vocabularySize = Object.keys(this.charIndices).length;
        this.indexChars = this.swap(this.charIndices);
        return this;
    }

    swap(json){
        var ret = {};
        for(var key in json){
            ret[json[key]] = key;
        }
        return ret;
    }

    argMax(array) {
        return Array.from(array).map((x, i) => [x, i]).reduce((r, a) => (a[0] > r[0] ? a : r))[1];
    }

    sample(preds, temperature=1.0) {
        const predsTemperature = preds.map((prob) => Math.exp(Math.log(prob) / temperature) );
        const mult = SJS.Multinomial(1, predsTemperature);
        return this.argMax(mult.draw());
    }

    generateSeed() {
        let text = '';
        for (let i = 0; i < this.maxLen; ++i) {
            const index = Math.floor(Math.random() * (this.vocabularySize-0.001));
            text += this.indexChars[index];
        }
        console.log('seed', text);
        return text;
    }

    textToTensor(text) {
        const inputText = text.toLowerCase();
        // Look up word indices.
        const inputBuffer = tf.buffer([1, this.maxLen, this.vocabularySize], 'float32');
        for (let i = 0; i < inputText.length; ++i) {
            const char = inputText[i];
            inputBuffer.set(1, 0, i, this.charIndices[char]);
        }
        return inputBuffer.toTensor();
    }

    predict(text, drunkenness, howManyNames) {

        let sentence = text;
        let generated = '';
        let namesSoFar = 0;
        let started = false;
        for (let i = 0; i < 150; ++i) {
            let input = this.textToTensor(sentence);

            const predictOut = this.model.predict(input);
            const preds = predictOut.dataSync();
            const nextIndex = this.sample(preds, drunkenness);
            const nextChar = this.indexChars[nextIndex];

            if (started) {
                if (nextChar.match(/\n+/g)) {
                    namesSoFar++;
                    if (namesSoFar == howManyNames) {
                        break;
                    }
                }
                generated += nextChar;
            }
            else if (nextChar.match(/[A-Z]/g)) {
                started = true;
                generated += nextChar;
            }

            sentence = sentence.slice(1) + nextChar;
        }

        return generated;
    }
};

let modelNames = {
    "Latinx": 'nombres-pill-cfx20',
    "Modern Latinx": 'nombres-cf-60',
    "English": 'english-25',
    "Original Latinx": 'nombres-og-alpha'
};

function removeNames(unsavedOnly = true) {
    const selector = unsavedOnly ? '[saved="nope"]' : '[saved]';
    const toRemove = document.querySelectorAll(selector);
    if (toRemove.length > 0) {
        for (let j = 0; j < toRemove.length; j++) {
            toRemove[j].remove();
        }
    }
}

/**
 * Loads the pretrained model and registers the predict
 * function with the UI.
 */
async function setupSentiment () {        
    
    const container = document.getElementById('container');
    const btnContainer = document.getElementById('btn-container');
    const favoritosBtn = document.getElementById('favoritos');
    const borrarBtn = document.getElementById('borrar');
    const selector = document.getElementById('select-model');
    const slider = document.getElementById('drunk-slider');
    const outList = document.getElementById('output-list');
    const tip = document.getElementById('tip');
    const primeroTip = document.getElementById('primero');
    const screenshotTip = document.getElementById('tip2');
    let numOfNames = 8;

    favoritosBtn.addEventListener('click', () => {
        removeNames();
        borrarBtn.style.display = 'block';
        screenshotTip.style.display = 'block';
    });
    borrarBtn.addEventListener('click', () => {
        removeNames(false);
        borrarBtn.style.display = 'none';
        screenshotTip.style.display = 'none';
    });

    for (const [label, modelName] of Object.entries(modelNames)) {
        const predictor = await new SentimentPredictor().init(modelName);

        const genBtnHtml = '<button id="gen-btn-' + modelName + '" style="display:none">dále</button>';
        btnContainer.insertAdjacentHTML('afterbegin',genBtnHtml);
        const genBtn = document.getElementById('gen-btn-'+ modelName);        

        const selectString = '<option value="'+modelName+'">'+label+'</option>';
        selector.insertAdjacentHTML('beforeend',selectString);

        genBtn.addEventListener('click', async () => {
            removeNames();
            primero.style.display = 'none';
            const drunkenness = slider.value / 100;
            const seed = predictor.generateSeed();
            const result = predictor.predict(seed,drunkenness,numOfNames);
            const names = result.split('\n');
            for (name of names) {
                outList.insertAdjacentHTML('beforeend','<li saved="nope">'+name+'</li>');
                const listItem = outList.lastElementChild;
                listItem.addEventListener('click', function () {
                    if (event.target.getAttribute("saved") == "yep") {
                        event.target.setAttribute("saved","nope");
                    }
                    else { 
                        event.target.setAttribute("saved","yep");
                        favoritosBtn.style.display = 'block';
                        tip.style.display = 'none';
                    }
                });
            };
            if (tip.style.display == 'none') {
                favoritosBtn.style.display = 'block';
            }
        });
    };

    document.getElementById('gen-btn-'+ selector.value).style.display = 'block';

    selector.addEventListener('change', function () {
        const allButtons = document.getElementsByTagName('button');
        for (let i = 0; i < allButtons.length; i++) {
            allButtons[i].style.display = 'none';
        }
        const genButton = document.getElementById('gen-btn-'+ selector.value);
        genButton.style.display = 'block';
    });


}

setupSentiment();
